:root {
  /*--primary-color:#F3CE00;*/
  --primary-color:#e6b141;


 /*--primary-color:linear-gradient(to right,#313B47,#00C8AA);*/
  --dark-color:  #056d5a;
  --dark-color:  #221e41;;
  --grey-color: #f5f7fa;
  --light-color: white;
  --card-color: #f5f7fa;
  --danger-color: #dc3545;
  --success-color: #28a745;
  --orange-color:  #056d5a;
}

* {
  box-sizing: border-box;
  margin: 0;
  
}



body {
  font-family: 'Poppins', sans-serif;
  font-size: 1rem;
  line-height: 1.6;
  background-color: #f5f7fa;
  background-color: var(--card-color); 
  background-color:  rgb(246, 246, 246);
  color: #221e41;;
   
}

a {
  color: white;
  color: var(--light-color);
  color:#221e41;;
  text-decoration: none;
  font-size: 1rem;
  font-weight: bolder;
}


ul {
  list-style: none;
}



/*home*/ 
.match{
  text-align: center;
  padding: 4rem;
  margin-top:4rem;
  font-weight: bold;
  font-size: 3rem;
}

.filter {
  display: grid;
  grid-template-columns: 2fr 2fr 2fr;
  align-items: center;
  grid-gap: 4rem;
  padding: 1rem;
  line-height: 1.8;
  margin-bottom: 1rem;
  border-radius: 5px;
  margin: 2rem 2rem 0 2rem;
  }

 /* cards container*/
.profile-user {
  display: grid;
  align-items: center;
  grid-template-columns: 1fr ;
  grid-gap: 10px;
}


/*cards*/

.profile {
  display: grid;
  grid-template-columns: 2fr 2fr 2fr;
  align-items: center;
  grid-gap: 4rem;
  padding: 1rem;
  line-height: 1.8;
  margin-bottom: 1rem;
  border-radius:5px;
  margin:2rem 2rem 0 2rem;
}

.card-color {
  background-color: #f5f7fa;
  background-color: var(--card-color);
  }

/* profile picture */
.img-profile { 
  border: 2rem grey;
  border-radius:10rem;
  height: 230px;
  width: 230px !important;
  object-fit: cover;
  object-position: top center;
}

.profile .img-profile {
height: 260px;
width: 260px !important;
}

.round-img {
  border-radius: 80%;
}

/*language -cards*/

.profile ul {
  display: inline-flex;
  justify-content: space-around;
  padding: 0, 2rem;
  border-radius: 0 0rem 2rem 2rem;
}

.location-smart {
  display: none;
}

.profile h3 {
  padding: 0rem;
}

/* profile settings*/ 

.p-icons-settings {
  padding:  0 0.5rem 0 0 ;
  display: inline;
}

.settings-btn {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  width:80%
}

/* Gridsystem */
 
.grid-layout {
  display: grid;
  grid-template-areas:
    'one one'
    'two two'
    'three four ';
    
  grid-gap: 1rem;
 
}

.grid-one {
  grid-area: one;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  grid-gap: 1rem;
}

/* Back To Profiles*/

.profile-button{
  grid-area: one ;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  grid-gap: 1rem;
}

.grid-one img {
  width: 250px;
}

.grid-one .icons a {
  color: #221e41;
  color: var(--dark-color);
  margin: 0 0.3rem;
}

.icons a {
  color: grey;
  margin: 0 0.3rem;
}


.grid-one .icons a:hover {
  color: #221e41;
  color: var(--dark-color);
}


/* show Profile --> goals */ 
.grid-two {
  grid-area: two;
  text-align: center;
  border-radius: 0 0 0.5rem 0.5rem ;
}

.grid-three {
  grid-area: three ;
  border-radius: 0.5rem;
}

.grid-three h2,
.grid-four h2 {
  margin-bottom: 1rem;
}

.grid-four {
  grid-area: four;
  border-radius: 0.5rem;     
}


/* MessageForm & Container*/ 

.message-form {
  grid-area: one ;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  grid-gap: 0.5rem; 
  padding: 0 3rem;
}

/* footer */ 

.footer p,
.footer a {
  color:grey;
  font-weight: 100;
  padding:0.2rem;
  align-items: center;
  font-size: 0.7rem;
  margin:auto;
  text-align: center;
}

@media (max-width: 1000px) {
  .grid-container {
    display: grid;
    grid-template-areas: "reg";
    grid-gap: 1rem;
    margin: 0 2rem;
  }


  .pic {
    display: none;
  }
}

@media (min-width: 1100px) {
  .container {
    margin-top: 8rem;
  }


  .max-width {
    margin: 0 auto;
    max-width: 1300px;
  }

  .max-width-userList {
    max-width: 80%;
    margin: 0 auto;
  }

  .max-width-landing {
    margin: 0 auto;
    max-width: 1100px;
  }

  a {
    color: black;
  }

  .profile .img-profile {
    height: 290px;
    width: 290px !important;
  }

  /* home */

  .match {
    padding: 1rem;
  }

  .filter {
    padding: 2rem 0rem !important;
    grid-template-columns: 2fr 2fr 2fr;
    text-align: center;
    grid-gap: 2rem;
  }

  /*cards*/
  .profile-user {
    display: grid;
    grid-template-columns: 2fr 2fr;
    align-items: center;
    line-height: 1.8;
    margin-bottom: 1rem;
    border-radius: 5px;
    border-color: #28a745;
    border-width: 3cm;
  }

  .profile {
    grid-template-columns: 1fr;
    text-align: center;
    grid-gap: 1.5rem;
    padding: 0rem;
    border-radius: 2rem;
  }

  .profile ul {
    border-radius: 0 0rem 2rem 2rem;
    padding: 2rem;
    flex-direction: row;
  }

  a:hover .profile ul {
    border-radius: 2rem;
    background-color: darkslateblue;
    border-radius: 2rem 0rem;
  }

  .card-buttons {
    display: none;
  }

  .location-smart {
    display: block;
  }

  /*grid-system*/

  .grid-four {
    grid-area: four;
    border-radius: 0.5rem;
  }

  .img-profile img,
  .grid-one img,
  .profile img {
    width: 200px;
    margin: auto;
    margin-top: 1rem;
  }

  /*profile settings*/
  .flexbox-settings-Large-Screen {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    align-items: center;
  }
}

@media (min-width: 650px) {
  ul .nav:hover {
    color: var(--primary-color) !important;
  }

  .filter {
    /*padding: 2rem 0rem !important;*/
    margin: 2rem 1rem 0 1rem !important;
  }
}

@media (min-width: 1380px) {
  /*cards*/
  .profile-user {
    grid-template-columns: 2fr 2fr 2fr;
  }


  .card-buttons {
    display: none;
  }

  .profile ul {
    border-radius: 0 0rem 2rem 2rem;
    padding: 2rem;
    flex-direction: row;
  }

  a:hover .profile ul {
    border-radius: 2rem;
    background-color: darkslateblue;
    border-radius: 2rem 0rem;
  }

  .profile {
    grid-template-columns: 1fr;
    text-align: center;
    grid-gap: 1rem;
    padding: 0rem;
    border-radius: 2rem;
  }

  .profile .img-profile {
    height: 290px;
    width: 290px !important;
  }

  .location-smart {
    display: block;
  }
}

@media (max-width: 816px) {
  .pic {
    display: none;
  }

  .grid-container {
    display: grid;
    grid-template-areas:
      "reg"
      "pic";
  }

