    padding: 0rem;
    border-radius: 2rem;
  }

  .card-buttons {
    display: none;
  }

  .filter {
    grid-template-columns: 1fr;
    text-align: center;
    grid-gap: 2rem;
  }

  /*settings*/
  .large {
    font-size: 2.5rem !important;
    padding: 1rem 0rem !important;
  }

  .message-form {
    padding: 0.3rem;
  }

