    margin-right: 0rem !important;
  }

  .btn-smart {
    display: inline-block;
    background: var(--light-color);
    color: #333;
    font-size: 1rem;
    border-radius: 4px;
    cursor: pointer;
    transition: opacity 0.2s ease-in;
    outline: none;
  }

  .hide-sm {
    display: none;
  }

  /* Text Styles */
  .x-large {
    font-size: 3rem;
  }

  .lead {
    font-size: 1rem;
  }

  .home-h1 {
    font-size: 1.6rem;
  }

  /* Profile Page */
  .grid-three img,
  .grid-one img,
  .profile img {
    width: 200px;
    margin: auto;
    margin-top: 2.6rem;
    padding: 0rem;
    display: flex;
  }

  .profile .img-profile {
    height: 270px;
    width: 270px !important;
  }

  /* grid-system*/

  .grid-layout {
    grid-template-areas:
      "one"
      "two"
      "three"
      "four";
  }

  .img-smart {
    display: none;
  }

  .profile ul {
    background-color: darkslateblue;
    border-radius: 0 2rem;
    padding: 1rem;
    flex-direction: row;
  }

  a:hover .profile ul {
    border-radius: 2rem 0rem;
    /*background-color:#e6b141;*/
  }

  .location-smart {
    display: block;
  }
}

@media (max-width: 360px) {
  .match h1 {
    display: none;
  }

  .smart-p {
    padding: 0.5rem;
  }
}

