.btn-primary,
.bg-primary,
.badge-primary,
.alert-primary {
  background: var(--primary-color);
  color: #fff;
}

.btn-light,
.bg-light,
.badge-light,
.alert-light {
  background: var(--light-color);
  color: #333;
}

.btn-dark,
.bg-dark,
.badge-dark,
.alert-dark {
  background: var(--dark-color);
  color: #fff;
}


.btn-danger,
.bg-danger,
.badge-danger,
.alert-danger {
  background: var(--danger-color);
  color: #fff;
}

.btn-success,
.bg-success,
.badge-success,
.alert-success {
  background: var(--success-color);
  color: #fff;
}

.btn-white,
.bg-white,
.badge-white,
.alert-white {
  background: #fff;
  color: #333;
 /* border: rgb(228, 228, 228) solid 1px; */
}

.btn:hover {
  opacity: 0.8;
}

.focus:focus {
  background-color: var(--dark-color)
}

.bg-light,
.badge-light {
  border: #fbfbfb;
  border-radius: 3rem;
}

/* Forms */
.form .form-group {
  margin: 1.2rem 10rem;
   padding: 0.3rem !important;
}

.form p, 
.grey {
  font-size: 0.8rem;
  margin-top:0.5rem;
}

.form .form-text {
  display: block;
  margin-top: 0.3rem;
  color: #888;
}

.form input[type='text'],
.form input[type='password'],
.form input[type='date'],
.form input[type='number'],
.form select,
.form textarea {
  display: block;
  width: 100%;
  padding: 0.4rem;
  padding-left:0.8rem;
  font-size: 1.1rem;
  border:  1px solid #ccc;
  transform: capitalize;
  border-radius:0.5rem;
  background-color: white;
}

.form input[type='submit'],
button {
  font: inherit;
}

.form input:hover[type='text'],
.form input:hover[type='password'],
.form input:hover[type='date'],
.form input:hover[type='number'],
.form input:hover[type='select'],
.form select:hover
 {
  color:var(--dark-color);
  font-weight:bold;
}

.form textarea:hover{ 
  color:var(--dark-color);
}


.animation {
  transition:0.15s;
}

.animation:hover {
  transform: scale(0.99) translateY(-3px);
  box-shadow: 0 10px 20px rgba(39, 42, 56, 0.2);
}


/* Navbar */
/* https://de.freepik.com/freie-ikonen/papagei_928764.htm*/

.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 2rem 4rem;
  position: absolute;
  z-index: 1;
  width: 100%;
  top: 0;
  background-color: rgb(246, 246, 246);
}

.bg-blue {
  border-radius: 10rem;
  padding: 0.2rem;
  background-color: var(--dark-color);
}

.navbar a {
  color: var(--dark-color);
  text-decoration: none;
  font-size: 1rem;
  font-weight: bolder;
  padding: 0.45rem;
  margin: 0 0.25rem;
}

nav img {
  width: 30px;
  height: 30px;
  margin: auto 0;
  opacity: 0.7;
}

.navbar h1 {
  font-size: 1.4rem;
}

.navbar ul {
  display: flex;
  justify-content: space-around;
}


.start {
  margin: auto 0;
  padding: 0 1rem;
  color: rgb(230, 218, 218);
}

/*icons*/
nav i {
  font-size: 3.5rem;
  color: var(--primary-color);
}

.msg i {
  font-size: 1.6rem;
  color: var(--primary-color);
  padding: 0.7rem;
}

.msg:hover,
.navbar h1:hover,
.profile-i:hover {
  transform: translateY(-3px);
}

.msg:hover {
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2);
}

.navbar a:hover {
  color: burlywood;
}



@media (max-width: 700px) {
